import React from 'react';
import { FoodCard, FoodRow } from '../compontents';
import { MOCK_FOOD_DB } from '../constants/mock-food';

export const FoodPage = () => {
  return (
    <>
      {MOCK_FOOD_DB.map((foodType, indexRow) => {
        return (
          <FoodRow key={indexRow} title={foodType.title}>
            {foodType.foodList.map((food, indexCard) => {
              return (
                <FoodCard
                  key={indexCard}
                  title={food.title}
                  imgSrc={food.imgSrc}
                  nutrition={food.nutrition}
                />
              );
            })}
          </FoodRow>
        );
      })}
    </>
  );
};
