import './App.scss';
import React, { useState, useEffect, useRef } from 'react';
import { Switch, Route, useLocation } from 'react-router-dom';
import { CreateRecipe, MyRecipes, MyProfile, IntroPage } from './pages/main';
import { Header, BottomNav } from './compontents/layout';

function App() {
  const [showIntro, setShowIntro] = useState(true);
  const header = useRef();
  const location = useLocation();

  useEffect(() => {
    location.pathname !== '/' ? setShowIntro(false) : setShowIntro(true);
  }, [location.pathname]);

  return (
    <div className='app'>
      <Header passRef={header} />

      <div className='app__content'>
        <Switch>
          <Route path='/my-profile' exact>
            <MyProfile />
          </Route>
          <Route path='/create-recipe'>
            <CreateRecipe header={header} />
          </Route>
          <Route path='/my-recipes'>
            <MyRecipes />
          </Route>
        </Switch>
      </div>

      {showIntro && <IntroPage />}

      <BottomNav />
    </div>
  );
}

export default App;
